  .contacts__map {
    background: url("../../img/index/rectangle-mob@2x.png") center center no-repeat;
    background-size: 320px auto;
  }
  .contacts__map::before {
    background: url("../../img/index/map-pin@2x.png") center center no-repeat;
    background-size: 57px;
  }
}
@media (min-width: 768px) {
